  }

  .site-list__item:first-child a {
    border-top: none;
    padding-top: 15px;
  }
  .site-list__item:last-child a {
    border-bottom: none;
    padding-bottom: 14px;
    box-shadow: inset 0 -7px 7px -2px rgba(0, 0, 0, 0.2);
  }
}
@media (min-width: 768px) {
  .site-list__item {
    display: flex;
    justify-content: center;
  }
  .site-list__item--gap {
