.main-nav__toggle {
  position: absolute;
  right: 20px;
  top: 17px;
  z-index: 10;
  width: 23px;
  height: 22px;
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
  outline: none;
}
.main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 23px;
  height: 4px;
  background-color: #81b3d2;
  box-shadow: 0 9px 0 0 #81b3d2, 0 18px 0 0 #81b3d2;
}
.main-nav__toggle:hover::before, .main-nav__toggle:focus::before {
  opacity: 0.6;
}
.main-nav__toggle:active::before {
  opacity: 0.3;
}

.main-nav--active .main-nav__toggle::before, .main-nav--active .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 9px;
  left: -2px;
  width: 27px;
  height: 4px;
  background-color: #ffffff;
  box-shadow: none;
}
.main-nav--active .main-nav__toggle::before {
  transform: rotate(45deg);
}
.main-nav--active .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav--active .main-nav__toggle:hover::before, .main-nav--active .main-nav__toggle:hover::after, .main-nav--active .main-nav__toggle:focus::before, .main-nav--active .main-nav__toggle:focus::after {
  opacity: 0.6;
}
.main-nav--active .main-nav__toggle:active::before, .main-nav--active .main-nav__toggle:active::after {
  opacity: 0.3;
}

@media (max-width: 767px) {
  .main-nav {
    position: absolute;
    top: 0;
    z-index: 10;
    width: 100%;
  }
  .main-nav--active .main-nav__list, .main-nav--nojs .main-nav__list {
    display: block;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }

  .main-nav__list {
    display: none;
  }
  .main-nav__list--active {
    display: block;
  }
}
@media (min-width: 768px) {
  .main-nav {
    position: static;
  }

  .main-nav__wrapper {
    width: 320px;
    margin: 0 auto;
    box-sizing: border-box;
  }
}
@media (min-width: 768px) and (min-width: 768px) {
